'use strict';

// MODULES //

import isObject from '@stdlib/assert-is-plain-object' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean' ;
import { isPrimitive as isPositiveInteger } from '@stdlib/assert-is-positive-integer' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {*} [options.thisArg] - execution context
* @param {PositiveInteger} [options.limit] - maximum number of pending invocations at any one time
* @param {boolean} [options.series] - boolean indicating whether to wait for a previous invocation to complete before invoking a provided function for the next element in a collection
* @returns {(Error|null)} null or an error object
*
* @example
* var opts = {};
* var options = {
*     'thisArg': {},
*     'series': false,
*     'limit': 10
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '0iQ2h', options ) );
	}
	if ( hasOwnProp( options, 'thisArg' ) ) {
		opts.thisArg = options.thisArg;
	}
	if ( hasOwnProp( options, 'series' ) ) {
		opts.series = options.series;
		if ( !isBoolean( opts.series ) ) {
			return new TypeError( format( '0iQ30', 'series', opts.series ) );
		}
	}
	if ( hasOwnProp( options, 'limit' ) ) {
		opts.limit = options.limit;
		if ( !isPositiveInteger( opts.limit ) ) {
			return new TypeError( format( '0iQ3b', 'limit', opts.limit ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
